import React from 'react'
import paper from '../../assets/icon-paper.svg'
import rock from '../../assets/icon-rock.svg'
import scissors from '../../assets/icon-scissors.svg'
import { useLocalStorage } from './useLocalStorage'

const GameContext = React.createContext()

function GameProvider(props) {
  const [showRules, setShowRules] = React.useState(false)
  const [score, saveScore] = useLocalStorage('SCORE_V1', '')
  const [points, setPoints] = React.useState(!score ? 0 : score)
  const [hideButtons, setHideButtons] = React.useState(true)
  const [hideBase, setHideBase] = React.useState(false)
  const [hideResult, setHideResult] = React.useState(false)
  const [results, setResults] = React.useState(false)
  const [selectedButtonType, setSelectedButtonType] = React.useState()
  const [selectedButtonStyle, setSelectedButtonStyle] = React.useState('')
  const [showButton, setShowButton] = React.useState('empty-circle')
  const [styleCPUButton, setStyleCPUButton] = React.useState()
  const [state, setState] = React.useState()
  const [showResult, setShowResult] = React.useState(false)

  const options = [
    paper,
    scissors,
    rock
  ]
  
  const random = () => Math.floor(Math.random()*(max - min)) + min

  const max = 3
  const min = 0
  const numberCPU = random()
  let numberUser

  const openRules = () => {
    setShowRules(true)
  }

  const paperClick = () => {
    setHideButtons(false)
    setHideBase(true)
    setResults(true)
    setHideResult(true)
    setSelectedButtonStyle('paper')
    setSelectedButtonType(paper)
    choiceCPU()
    numberUser = 0
  }

  const rockClick = () => {
    setHideButtons(false)
    setHideBase(true)
    setResults(true)
    setHideResult(true)
    setSelectedButtonStyle('rock-clicked')
    setSelectedButtonType(rock)
    choiceCPU()
    numberUser = 2
  }

  const scissorsClick = () => {
    setHideButtons(false)
    setHideBase(true)
    setResults(true)
    setHideResult(true)
    setSelectedButtonStyle('scissors-clicked')
    setSelectedButtonType(scissors)
    choiceCPU()
    numberUser = 1
  }

  const choiceCPU = () => {
    setTimeout(() => {
      setHideBase(false)
      if (numberCPU === 0) {
        setShowButton(options[numberCPU])
        setStyleCPUButton('paper-cpu')
      }
      else if (numberCPU === 1) {
        setShowButton(options[numberCPU])
        setStyleCPUButton('scissors-cpu') 
      }
      else if (numberCPU === 2) {
        setShowButton(options[numberCPU])
        setStyleCPUButton('rock-cpu')
      }
      setTimeout(() => {
        if (numberCPU === numberUser) {
          setState('Draw')
          setShowResult(true)
        } else if (numberCPU === 0 && numberUser === 1) {
          setState('You Win')
          setShowResult(true)
          setPoints(points + 1)
        } else if (numberCPU === 0 && numberUser === 2) {
          setState('You Lose')
          setShowResult(true)
          setPoints(points === 0 ? 0 : points - 1)
        } else if (numberCPU === 1 && numberUser === 0) {
          setState('You Lose')
          setShowResult(true)
          setPoints(points === 0 ? 0 : points - 1)
        } else if (numberCPU === 1 && numberUser === 2) {
          setState('You Win')
          setShowResult(true)
          setPoints(points + 1)
        } else if (numberCPU === 2 && numberUser === 0) {
          setState('You Win')
          setShowResult(true)
          setPoints(points + 1)
        } else if (numberCPU === 2 && numberUser === 1) {
          setState('You Lose')
          setShowResult(true)
          setPoints(points === 0 ? 0 : points - 1)
        }
      }, 1000)
    }, 1500)
  }

  const comeBack = () => {
    setHideButtons(true)
    setHideBase(false)
    setHideResult(false)
    setResults(false)
    setSelectedButtonType()
    setSelectedButtonStyle('')
    setShowButton('empty-circle')
    setStyleCPUButton()
    setState()
    setShowResult(false)
  }

  React.useEffect(() => {
    saveScore(points)
  })

  return (
    <GameContext.Provider value={{
      points,
      hideButtons,
      hideBase,
      paperClick,
      rockClick,
      scissorsClick,
      paper,
      scissors,
      rock,
      results,
      hideResult,
      selectedButtonType,
      selectedButtonStyle,
      showButton,
      showResult,
      state,
      styleCPUButton,
      comeBack,
      showRules,
      setShowRules,
      openRules,
    }}>
      {props.children}
    </GameContext.Provider>
  )
}

export { GameContext, GameProvider }
